$cellSize: 32px;

@mixin crossedLine($deg) {
  content: '';
  width: 2px;
  height: 100%;
  background: red;
  position: absolute;
  top: 0;
  left: 45%;
  transform: rotate($deg);
}

@mixin value($color) {
  background-color: lightgrey;
  color: $color;
}

.Cell {
  display: inline-block;
  width: $cellSize;
  height: $cellSize;
  float: left;
  border-top: var(--cellBorder);
  border-left: var(--cellBorder);
  background-color: darkgrey;
  font: inherit;
  font-size: 18px;
  font-weight: bold;
  line-height: $cellSize;
  text-align: center;

  &__empty {
    background-color: lightgrey;
  }

  &__busted {
    background-color: red;
  }

  &__incorrect {
    position: relative;

    &::before {
      @include crossedLine(-45deg);
    }

    &::after {
      @include crossedLine(45deg);
    }
  }

  &__1 {
    @include value(blue);
  }

  &__2 {
    @include value(green);
  }

  &__3 {
    @include value(red);
  }

  &__4 {
    @include value(purple);
  }

  &__5 {
    @include value(maroon);
  }

  &__6 {
    @include value(turquoise);
  }

  &__7 {
    @include value(black);
  }

  &__8 {
    @include value(darkgrey);
  }
}
