* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Comic Sans MS, cursive, sans-serif;
  background-color: lightgrey;
}

a {
  color: inherit;
}

button {
  border-style: none;
  cursor: pointer;

  &:focus {
    outline: 0;
  }
}

:root {
  --headerHeight: 38px;
  --footerHeight: 28px;
  --cellBorder: 1px solid black;
}
