.MineField {
  margin: 4px 0 8px;

  &Row {
    &__count {
      @each $count in 9, 16, 30 {
        &-#{$count} {
          .Cell {
            &:nth-child(#{$count + 'n'}) {
              border-right: var(--cellBorder);
            }

            &:nth-last-child(#{'-n+' + $count}) {
              border-bottom: var(--cellBorder);
            }
          }
        }
      }
    }

    &__disabled {
      pointer-events: none;
    }
  }
}
