.Header {
  height: var(--headerHeight);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  font-size: 22px;
  color: white;

  &Title,
  &Link {
    flex-grow: 1;
    text-align: right;
  }

  &Title {
    margin: 0 0 0 38px;
  }

  &Link {
    padding: 0 16px 0 0;
  }
}
